  /* CATEGORIES SECTION */
  .categories {
    display: flex;
    flex-direction: row;
    margin-left: 3%;
    flex-wrap: wrap;
  }
  .indian div{
    width: 30%;
  }
  @media screen and (max-width: 964px) {
    .categories div {
      width: 50%;
    }
    .indian div{
      width: 50%;
    }
  }
  
  .categories div img {
    transition: all 0.5s ease-in-out;
  }
  
  .categories div img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  /* INDIAN COLTHES SECTION */
  .indian {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0% 5%;
  }
  .indian img {
    height: auto;
    width: 90%;
    grid-row-gap: 10px;
    row-gap: 10px;
    transition: all 0.1s ease-in-out;
  }
  .indian img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  /* ACCESSORIES SECTION */
  .acc {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 2% 5%;
  }
  .acc div {
    width: 33%;
  }
  
  @media screen and (max-width:850px) {
    .acc div{
      width: 50%;
    }
  }
  
  .flexboxes div {
    margin-top: 20px;
  }
  
  .flexboxes div img:hover {
    cursor: pointer;
  }
  
  .categories div img:hover {
    cursor: pointer;
  }
  
  .indian div {
    margin-top: 30px;
  }
  
  /* CHANGING CURSOR STYLE WHEN HOVERNG OVER CARDS */
  .indian div img:hover {
    cursor: pointer;
  }
  
  .indian,
  .categories {
    width: 100%;
    align-items: center;
  }
  
.products{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 20px;
}

.products .item{
    border: 1px solid black;
    /* width: 20%; */
    /* height: 400px; */
    margin: 20px;
}

.column img {
	display: block;
	width: 100%;
    height: 100%;
}

.column#caption {
	position: relative;
    width: 20%;
}
.column#caption .text {
		position: absolute;		
        top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		z-index: 10;
        opacity: 0;
        transition: all 0.8s ease;
}
.column#caption .text h1 {		
		margin: 0;
}
.column#caption:hover .text {
	opacity: 1;
    color: black;
	
}
.column#caption:hover img {
    opacity: 0.6;
}
